import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanStockCard = (data, mulai_dari, sampai_dengan) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  doc.setFontSize(15);
  doc.text("LAPORAN STOCK - CARD ", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);
  doc.setFontSize(10);
  doc.text(`Mulai Dari : ${mulai_dari}`, 14, 39);
  //   row 2
  doc.text(`Sampai Dengan	: ${sampai_dengan}`, 100, 39);
  data.forEach((item, index) => {
    let tableColumn = [
      [
        {
          content: `KODE LOKASI : ${item.kode_lokasi}`,
          colSpan: 6,
        },
        {
          content: `KODE MODEL : ${item.kode_jenis}`,
          colSpan: 5,
        },
      ],
      [
        {
          content: `TANGGAL`,
          styles: {cellWidth : 18}
        },
        {
          content: `AWAL\nNETT`,
          styles: { halign: "right", cellWidth : 13 },
        },
        {
          content: `AWAL\nBRT`,
          styles: { halign: "right", cellWidth : 13 },
        },
        {
          content: `IN\nNETT`,
          styles: { halign: "right", cellWidth : 13 },
        },
        {
          content: `IN\nBRT`,
          styles: { halign: "right", cellWidth : 13 },
        },
        {
          content: `OUT\nNETT`,
          styles: { halign: "right", cellWidth : 13 },
        },
        {
          content: `OUT\nBRT`,
          styles: { halign: "right", cellWidth : 13 },
        },
        {
          content: `AKHIR\nNETT`,
          styles: { halign: "right", cellWidth : 13 },
        },
        {
          content: `AKHIR\nBRT`,
          styles: { halign: "right", cellWidth : 15 },
        },
        {
          content: `KETERANGAN`,
          styles : {cellWidth : 25}
        },

        {
          content: `NO BON`,
        },
      ],
    ];
    item.detail.forEach((hasil) => {
      let rows = [
        hasil.tanggal,
        {
          content: formatGram(hasil.awal_netto, 3),
          styles: { halign: "right" },
        },
        { content: formatGram(hasil.awal_bruto), styles: { halign: "right" } },
        { content: formatGram(hasil.in_netto, 3), styles: { halign: "right" } },
        { content: formatGram(hasil.in_bruto), styles: { halign: "right" } },
        {
          content: formatGram(hasil.out_netto, 3),
          styles: { halign: "right" },
        },
        { content: formatGram(hasil.out_bruto), styles: { halign: "right" } },
        {
          content: formatGram(hasil.akhir_netto, 3),
          styles: { halign: "right" },
        },
        { content: formatGram(hasil.akhir_bruto), styles: { halign: "right" } },
        hasil.keterangan,
        hasil.no_ext,
      ];
      tableRows.push(rows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      startY: 45,
      theme: "plain",
      rowPageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineColor: "#000",fontSize : 8, },
      headStyles: {
        lineColor: "#000",
        fontSize : 8,
        fillColor: [212, 212, 211],
      },
      columnStyles: {
        10: { cellWidth: 40 },
        11: { cellWidth: 40 },
        // etc
      },
    });
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN STOCK - CARD",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN STOCK - CARD</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanStockCard;
