import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday } from "../../getDate";
import { formatGram, formatRp, getUserID, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPindahBarang = (
  mulai_dari = "",
  sampai_dengan = "",
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 40;
  doc.setFontSize(18);
  doc.text("LAPORAN PINDAH BARANG", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);

  doc.setFontSize(10);
  doc.text(`Mulai Dari : ${mulai_dari}`, 14, 39);
  //   row 2
  doc.text(`Sampai Dengan	: ${sampai_dengan}`, 100, 39);
  data.forEach((list, index) => {
    tableColumn = [
      [
        {
          content: `NO BON : ${list.no_bon}`,
          colSpan: 6,
        },
      ],
      [
        {
          content: `TANGGAL : ${list.tanggal}`,
          colSpan: 6,
        },
      ],
      [
        {
          content: `LOKASI ASAL : ${list.lokasi_asal}`,
          colSpan: 3,
        },
        {
          content: `LOKASI TUJUAN : ${list.lokasi_tujuan}`,
          colSpan: 3,
        },
      ],
      [
        {
          content: `KETERANGAN : ${list.keterangan}`,
          colSpan: 6,
        },
      ],
      [
        {
          content: `NO`,
        },
        {
          content: `KODE JENIS`,
        },
        {
          content: `NAMA JENIS`,
        },
        {
          content: `BRUTO`,
          styles: { halign: "right" },
        },
        {
          content: `KODE TUKAR`,
          styles: { halign: "right" },
        },
        {
          content: `NETTO`,
          styles: { halign: "right" },
        },
        {
          content: `ONGKOS KIRIM`,
          styles: { halign: "right" },
        },
      ],
    ];
    list.detail_barang.forEach((barang, index) => {
      let rows = [
        ++index,
        barang.kode_jenis,
        barang.nama_jenis,
        { content: formatGram(barang.bruto), styles: { halign: "right" } },
        { content: barang.kadar_modal, styles: { halign: "right" } },
        { content: formatGram(barang.netto, 3), styles: { halign: "right" } },
        { content: formatRp(barang.ongkos_rp), styles: { halign: "right" } },
      ];
      tableRows.push(rows);
      console.log(tableRows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      foot: footRows,
      startY: index === 0 ? 45 : finalY + 4,
      theme: "plain",
      rowPageBreak: "avoid",
      pageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineColor: "#000",fontSize : 8, },
      headStyles: {
        lineColor: "#000",fontSize : 8,
        fillColor: [212, 212, 211],
      },
    });
    finalY = doc.autoTableEndPosY();
    tableRows = [];
    footRows = [];
  });

  doc.text(`Cetak By	: ${getUserID()}`, 14, finalY + 10);
  doc.text(`Cetak Date : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN PINDAH BARANG",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PINDAH BARANG</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPindahBarang;
