import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday } from "../../getDate";
import { formatGram, getUserID, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPrintMutasi = (
  row1isi = "",
  row2isi = "",
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let finalY = 40;
  let tableColumn = [];
  doc.setFontSize(15);
  doc.text("LAPORAN MUTASI STOCK", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);

  doc.setFontSize(10);
  //row 1
  doc.text(`Mulai Dari : ${row1isi}`, 14, 39);
  //   row 2
  doc.text(`Sampai Dengan	: ${row2isi}`, 100, 39);
  //   row 2
  // doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((barang, index) => {
    tableColumn = [
      [
        {
          content: `No Bon : ${barang.no_bon}`,
          colSpan: 4,
        },
        {
          content: `Keterangan : ${barang.keterangan}`,
          colSpan: 4,
        },
      ],
      [
        {
          content: `Kode Jenis`,
        },
        {
          content: `Bruto`,
          styles: { halign: "right" },
        },
        {
          content: `Kode Tukar`,
          styles: { halign: "right" },
        },
        {
          content: `Netto`,
          styles: { halign: "right" },
        },
        {
          content: `<====>`,
        },
        {
          content: `Kode Jenis 2`,
        },
        {
          content: `Kode Tukar 2`,
          styles: { halign: "right" },
        },
        {
          content: `Netto 2`,
          styles: { halign: "right" },
        },
      ],
    ];
    barang.detail_barang.forEach((data) => {
      let rows = [
        data.kode_jenis,
        { content: formatGram(data.bruto), styles: { halign: "right" } },
        { content: data.kadar_jual, styles: { halign: "right" } },
        { content: formatGram(data.netto, 3), styles: { halign: "right" } },
        "<====>",
        data.kode_jenis2,
        { content: data.kadar_jual2, styles: { halign: "right" } },
        { content: formatGram(data.netto2, 3), styles: { halign: "right" } },
      ];
      tableRows.push(rows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      foot: footRows,
      startY: index === 0 ? 45 : finalY + 10,
      theme: "plain",
      margin: { top: 20 },
      bodyStyles: { lineColor: "#000",fontSize : 8, },
      headStyles: {
        lineColor: "#000",fontSize : 8,
        fillColor: [212, 212, 211],
      },
    });
    finalY = doc.autoTableEndPosY() + 10;
    tableRows = [];
    tableColumn = [];
  });

  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  doc.text(`User    : ${getUserID()}`, 14, finalY);
  doc.text(`Cetak	  : ${getToday()}`, 14, finalY + 5);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN MUTASI STOCK",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN MUTASI STOCK</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPrintMutasi;
