import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanStockPerKategori = (data) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let tableColumn = [];
  doc.setFontSize(15);
  doc.text("LAPORAN STOCK PER JENIS", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);

  doc.setFontSize(10);

  data.forEach((item, index) => {
    tableColumn = [
      [
        {
          content: `KODE JENIS : ${item.kode_kategori}`,
          colSpan: 4,
        },
      ],
      [
        {
          content: `KODE LOKASI`,
        },
        {
          content: `KODE MODEL`,
        },
        {
          content: `TOTAL BRUTO`,
          styles: { halign: "right" },
        },
        {
          content: `TOTAL NETTO`,
          styles: { halign: "right" },
        },
      ],
    ];
    item.detail.forEach((hasil) => {
      let rows = [
        hasil.kode_lokasi,
        hasil.kode_jenis,
        { content: formatGram(hasil.total_bruto), styles: { halign: "right" } },
        {
          content: formatGram(hasil.total_netto, 3),
          styles: { halign: "right" },
        },
      ];
      tableRows.push(rows);
    });
  });
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: 45,
    theme: "plain",
    rowPageBreak: "avoid",

    margin: { top: 20 },
    bodyStyles: { lineColor: "#000",fontSize : 8, },
    headStyles: {
      lineColor: "#000",fontSize : 8,
      fillColor: [212, 212, 211],
    },
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN STOCK PER JENIS",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN STOCK PER JENIS</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanStockPerKategori;
