import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPacking = (data) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let finalY = 30;
  let tableColumn = [];
  let footer = [];
  doc.setFontSize(15);
  doc.text("LAPORAN PACKING BARANG", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);

  doc.setFontSize(10);
  // let detail_barang = [];
  // data.map((packingL) => {
  //   packingL.packing_m.map((packingM) => {
  //     packingM.packing_s.map((packingS) => {
  //       detail_barang.push(...packingS.detail_barang);
  //       return true;
  //     });
  //     return true;
  //   });
  // });
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((packingL, index) => {
    tableColumn = [
      [
        {
          content: `TANGGAL : ${packingL.tanggal}`,
          colSpan: 2,
        },
        {
          content: `KODE CUSTOMER : ${packingL.kode_customer}`,
          colSpan: 2,
        },
        {
          content: `NOMOR EXT PACKING: ${packingL.no_ext_packing}`,
          colSpan: 3,
        },
      ],
      [
        {
          content: `NOMOR PO : ${
            packingL.no_po === "-" ? "-" : packingL.no_po
          }`,
          colSpan: 7,
          styles: {
            lineColor: "#000",
            fillColor: packingL.status_valid
              ? [138, 182, 138]
              : [226, 151, 146],
          },
        },
      ],
      [
        {
          content: `TOTAL ITEM : ${packingL.total_item}`,
          colSpan: 2,
        },
        {
          content: `BERAT PACKING : ${formatGram(packingL.berat_packing)}`,
          colSpan: 2,
        },
      ],
      [
        {
          content: `KODE JENIS`,
        },
        {
          content: `CAP KODE`,
          styles: { halign: "right", cellWidth: 16 },
        },
        {
          content: `KODE TUKAR`,
          styles: { halign: "right", cellWidth: 16 },
        },
        {
          content: `BRUTO`,
          styles: { halign: "right" },
        },
        {
          content: `PKG`,
          styles: { halign: "right" },
        },
        {
          content: `NETTO`,
          styles: { halign: "right" },
        },
        {
          content: `DISC`,
          styles: { halign: "right" },
        },
        {
          content: `NETTO DISC`,
          styles: { halign: "right" },
        },
      ],
    ];
    packingL.detail_barang.forEach((hasil, index) => {
      let rows = [
        hasil.kode_jenis,
        { content: hasil.kadar_cetak, styles: { halign: "right" } },
        { content: hasil.kadar_jual, styles: { halign: "right" } },
        { content: formatGram(hasil.bruto), styles: { halign: "right" } },
        { content: formatGram(hasil.pkg), styles: { halign: "right" } },
        {
          content: formatGram(hasil.netto, 3),
          styles: { halign: "right" },
        },
        { content: hasil.disc, styles: { halign: "right" } },
        {
          content: formatGram(hasil.netto_disc, 3),
          styles: { halign: "right" },
        },
      ];
      tableRows.push(rows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      foot: footer,
      startY: index === 0 ? 45 : finalY,
      theme: "plain",
      rowPageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineColor: "#000",fontSize : 8, },
      headStyles: {
        lineColor: "#000",fontSize : 8,
        fillColor: [212, 212, 211],
      },
    });
    finalY = doc.autoTableEndPosY() + 10;
    tableRows = [];
  });
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN PACKING BARANG",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PACKING BARANG</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPacking;
